@media (min-width: 900px) {
	.delivery-payment-payment {
		padding: 50px 0;
		&__wrap > p {
			font-size: 18px; }
		&__anchor {
			display: none; }
		&__wrap > div {
			padding: 40px 0; }
		&__wrap h3 {
			font-size: 20px; }
		&__wrap h3 img {
			width: auto; }
		&__wrap p {
			font-size: 18px; }
		&__wrap #payCard a {
			margin-top: 5px;
			font-size: 16px;
			font-style: italic;
			margin-bottom: 18px; }
		&__wrap #payCard a:hover,
		&__wrap #payCard a:focus {
			color: #442751; }
		&__cards img {
			margin-right: 10px; }
		&__wrap ul li {
			align-items: center; }
		&__wrap ul li:nth-child(2) {
			max-width: 840px; }
		&__wrap ul li:nth-child(3) {
			max-width: 600px; }
		&__wrap ul span {
			padding: 16px;
			margin-right: 20px;
			font-size: 18px; }
		&__wrap #payTransfer > p {
			max-width: 665px; }
		&__subtext {
			margin: 30px 0; }
		&__subtext img {
			height: auto;
			margin-right: 10px; }
		&__subtext p {
			font-size: 18px;
			max-width: 616px; }
		&__subtext p:last-child {
			max-width: 702px; }
		&__info p {
			font-size: 18px;
			margin: 0;
			margin-bottom: 8px;
			max-width: 524px; } } }
